.App {
  color: #ddd;
  font-family: Consolas, Menlo, monospace;
  font-weight: 200;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  text-transform: lowercase;

  max-width: 1700px;
  margin: 0 auto;
  padding: 16px 0 0 16px;
}

header {
  display: flex;
  flex-direction: row;

  margin: 16px 0 32px;
}

header h1 {
  margin: 0 16px 0 0;
  padding: 0;

  font-size: 32px;
}

header span {
  font-size: 32px;
  margin: 0 4px 0 0;

  opacity: 0.3;
  cursor: pointer;
}

header span.active {
  opacity: 1;
}

.Records {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.Records .hide {
  display: none;
}

@media only screen and (max-width: 600px) {
  header {
    flex-wrap: wrap;
  
    margin: 8px 0 16px;
  }

  header h1 {
    width: 100%;

    font-size: 24px;
  }
}
.Record {
    width: 200px;
    height: 200px;
    margin: 0 16px 16px 0;

    background: black;
    box-sizing: border-box;

    position: relative;

    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Record.empty {
    border: 1px #ddd dotted;
}

.Record img {
    width: 200px;
    height: 200px;

    object-fit: cover;
}

.Record .Details {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 16px;

    background: rgba(16, 16, 16, 0.75);
}

.Record .Details h1, .Record .Details h2 {
    margin: 0;
    padding: 0;
    font-size: 16px;
}

.Record .Details h2 {
    color: #aaa;
}

.Record .Details .emoji {
    position: absolute;
    bottom: 2px;
    right: 8px;
    color: white;
    font-size: 32px;
}

@media only screen and (max-width: 600px) {
    .Record {
        width: 43.5vw;
        height: 43.5vw;
    }

    .Record img {
        width: 100%;
        height: 100%;
    }

    .Record .Details {
        padding: 4px;
    }

    .Record .Details h1, .Record .Details h2 {
        font-size: 16px;
    }
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  background: black;
}

